<template>
  <div class="detail-info">
    <div class="top-desc">{{ detailInfo.desc }}</div>
    <div class="detail" v-for="(items, index) in detailList" :key="index">
      <div class="type">{{ items.key }}</div>
      <div class="desc">{{ items.desc }}</div>
      <img v-for="(item, idx) in items.list" :key="idx" :src="item" alt="" />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    detailInfo: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  components: {},
  data() {
    return {};
  },
  computed: {
    //   计算属性不可改变原变量、对象、操作DOM
    detailList: function() {
      if (this.detailInfo.detailImage) {
        //   return this.detailInfo.detailImage.slice().reverse();
        return [...this.detailInfo.detailImage].reverse();
      }
      return [];
    },
  },
  watch: {},
  methods: {},
  created() {},
  mounted() {},
};
</script>

<style lang="less" scoped>
.detail-info {
  width: 100%;
  font-size: 18px;

  .top-desc {
    padding: 10px;
    line-height: var(--line-height);
  }

  .detail {
    .desc {
      padding: 10px;
      line-height: var(--line-height);
    }

    .type {
      padding: 10px;
    }
    img {
      width: 100%;
    }
  }
}
</style>
