<template>
  <!-- 制作一个框架包裹slider -->
  <div class="swiper">
    <!-- 配置slider组件 -->
    <slider ref="slider" :options="options">
      <slideritem
        v-for="(item, index) in banners"
        :key="index"
        class="swiper-item"
      >
        <img :src="item" alt="" @load="swiperImgLoad" />
      </slideritem>
      <!-- 设置loading,可自定义 -->
      <div slot="loading">loading...</div>
    </slider>
  </div>
</template>

<script>
import { slider, slideritem } from "vue-concise-slider";

export default {
  props: {
    banners: {
      type: Array,
      default: function() {
        return [];
      },
    },
  },
  components: {
    slider,
    slideritem,
  },
  data() {
    return {
      //滑动配置[obj]
      options: {
        currentPage: 0, //当前页码
        thresholdDistance: 500, //滑动判定距离
        thresholdTime: 100, //滑动判定时间
        autoplay: 1500, //自动滚动[ms]
        loop: true, //循环滚动
        direction: "horizontal", //方向设置，垂直滚动
        loopedSlides: 1, //无限滚动前后遍历数
        slidesToScroll: 1, //每次滑动项数
        timingFunction: "ease", // 滑动方式
        speed: 300, // 滑动时间
        preventDocumentMove: true, //阻止touch事件触发时，整个页面的滚动
        isLoad: false,
      },
    };
  },
  filters: {
    httpPrefix: function(value) {
      if (!value || value.toString().indexOf("http") == 0) {
        return value;
      }
      return "http:" + value;
    },
  },
  computed: {},
  methods: {
    swiperImgLoad() {
      if (!this.isLoad) {
        this.$emit("swiperImgLoad");
        this.isLoad = true;
      }
    },
    // 轮播监听事件
    // slide(data) {
    //   console.log(data);
    // },
    // onTap(data) {
    //   console.log(data);
    // },
    // onInit(data) {
    //   console.log(data);
    // },
  },
  created() {},
  mounted() {},
};
</script>

<style lang="less" scoped>
.swiper {
  width: 100%;
  height: 300px;
  .swiper-item {
    img {
      width: 100%;
    }
  }
}
</style>
