<template>
  <div class="detail">
    <detail-nav></detail-nav>
    <scroll-view ref="scroll" class="scroll-wrapper" :data="data">
      <detail-swiper :banners="banners" class="swiper" />
      <goods-info :goods="goods"></goods-info>
      <shop-info :shop="shop"></shop-info>
      <goods-desc :detail-info="detailInfo"></goods-desc>
      <goods-params :goods-params="goodsParams"></goods-params>
    </scroll-view>
  </div>
</template>

<script>
import {
  getGoodsDetail,
  Goods,
  GoodsParamsInfo,
  Shop,
} from "../../network/detail";

import DetailNav from "./DetailNav.vue";
import ScrollView from "../../components/scroll/ScrollView.vue";
import DetailSwiper from "../../components/swiper/DetailSwiper.vue";
import ShopInfo from "./ShopInfo.vue";
import GoodsInfo from "./GoodsInfo.vue";
import GoodsDesc from "./GoodsDesc.vue";
import GoodsParams from "./GoodsParams.vue";

export default {
  name: "Detail",
  components: {
    DetailNav,
    ScrollView,
    DetailSwiper,
    ShopInfo,
    GoodsInfo,
    GoodsDesc,
    GoodsParams,
  },
  data() {
    return {
      id: "",
      data: [],
      banners: [],
      goods: {},
      shop: {},
      detailInfo: {},
      goodsParams: {},
    };
  },
  computed: {},
  methods: {},
  created() {
    this.id = this.$route.params.id;
    getGoodsDetail(this.id).then((response) => {
      console.log(response);
      this.banners = response.result.itemInfo.topImages;
      this.goods = new Goods(
        response.result.itemInfo,
        response.result.columns,
        response.result.shopInfo.services
      );
      this.shop = new Shop(response.result.shopInfo);
      this.detailInfo = response.result.detailInfo;
      this.goodsParams = new GoodsParamsInfo(
        response.result.itemParams.info,
        response.result.itemParams.rule
      );
    });
  },
  mounted() {},
};
</script>

<style lang="less" scoped>
.detail {
  height: 100vh;
  position: relative;
  z-index: 20;
  background-color: #fff;
  .scroll-wrapper {
    overflow: hidden;
    position: absolute;
    top: 44px;
    left: 0;
    right: 0;
    bottom: 0;
  }
}
</style>
